import React from "react";
import DesktopLayout from "../components/WebDesktop/DesktopLayout";
import Link from "next/link";
import Card from "../components/card";
import { subscribe } from "../business/state";
import { fetchUserBySerial } from "../business/transactionBusiness";

class Index extends React.Component {  
  constructor(props){
    super(props);
    
  }

  render() {
    return (
      <DesktopLayout>
        <User/>        
      </DesktopLayout>
    );
  }
}

class User extends React.Component {  
  constructor(props){
    super(props);
    this.state= {user:{}};
  }

  componentDidMount(){
    subscribe("onSearch",(serial)=>{
      var user = fetchUserBySerial(serial);
      this.setState({user});
    })
  }

  render() {



    return (      
        <React.Fragment>
          <div>nombre: {this.state.user.nombre} </div>
          <div>Merchant Code: <input></input></div>
          <div><button>Registrar</button></div>
        </React.Fragment>
    );
  }
}



export default Index;
